<template>
  <div id="x_policy">
    <div class="out_line">
      <div class="hot_search">
        <x_hotsearch></x_hotsearch>
      </div>

      <!--左边长列表-->
      <div class="box1-left">
        <div class="box2-left">
           <x_currentlocation v-bind:current_where="current_place1" v-bind:last_location="last_location" v-bind:last_last_location="last_last_location"></x_currentlocation>
        </div>
        <div class="box3-left" >
          <x_cahrtlist_single :morelist="morelist" :url_last="url_last"></x_cahrtlist_single>
        </div>
        <!--下面分页插件-->
        <div class="box-paging">
         <paging :pagenumber="pagenumber" :url_last="url_last"  v-on:backnumber="backnumber_1"></paging>
        </div>
      </div>
      <!--右边长列表-->
      <div class="box4-right">
        <div class="box5-right"><x_industry></x_industry></div>
        <div class="box6-right"><x_tab_switch v-bind:tabt1="tabthree1"></x_tab_switch></div>
        <div class="box7-right"><x_tab_switch v-bind:tabt1="tabthree2"></x_tab_switch></div>
        <div class="box8-right"><x_singlesidebar v-bind:tab_three1="tabsingle2"></x_singlesidebar></div>
      </div>
    </div>
  </div>
</template>

<script>

import MHeader from "@/components/common/m-header";
import x_currentlocation from "../news/currentlocation.vue";
import x_singlesidebar from "../news/single_sidebar.vue";
import x_tab_switch from "../news/x_tab_switch.vue";
import x_hotsearch from "@/components/common/hotsearch.vue";
import Title from "@/components/news/Title.vue";
import x_Text from "@/components/news/Text.vue";
import x_related_reading from "@/components/news/related_reading.vue";
import x_statement from "@/components/news/x_statement.vue";
import x_comment from "@/components/news/comment.vue";
import x_chartlist from "@/components/news/chartlist.vue";
import x_cahrtlist_single from "@/components/news/charlist_single.vue";
import x_industry from "@/components/news/x_industry.vue";
import paging from "@/components/common/paging.vue";
import Qs from 'qs'

export default {
  name: "policy_text",
  data() {
    return {
      last_location: "政策",
      last_last_location: "",
      current_place1: "",
      tabthree1: [
        {
          message: "用户中心"
        },
        {
          message: "订阅资讯"
        },
        {
          message: "我的交易"
        }
      ],
      tabthree2: [
        {
          message: "近期活动"
        },
        {
          message: "协会活动"
        },
        {
          message: "其他"
        }
      ],
      tabsingle1: "相关热点",
      tabsingle2: "广告招商",
      morelist:{},
      pagenumber:{},
      url_last:"/information/findPolicyByPage",
    };
  },
  components: {
    MHeader,
    x_industry,
    x_currentlocation,
    x_singlesidebar,
    x_tab_switch,
    x_hotsearch,
    Title,
    x_Text,
    x_related_reading,
    x_statement,
    x_comment,
    x_chartlist,
    paging,
    x_cahrtlist_single
  },
  methods:{
      backnumber_1(val){
      this.morelist=val;
      this.pagenumber=val;
      console.log(val);
    },
    init() {
      var data_1 =Qs.stringify({
          pageNum:1 //页数，默认为1
        });
      this.axios({
        method: "post",
        url: this.HOST+"/information/findPolicyByPage",  //这里改为policy?
        data: data_1
      })
        .then(response => {
          console.log(response.data);
          this.morelist=response.data.message;
          this.pagenumber=response.data.message;
          console.log(this.pagenumber);
        })
          .then(response => {
            console.log(response.data);
          })
          .catch(function(response) {
            console.log(response.data);
          });
      }
  },
  activated(){
    this.init();
  }
};
</script>

<style lang="scss" scoped>
@mixin adv {
  margin: 5px auto;
  width: 1200px;
  height: 125px;
  overflow: hidden;
}
@mixin w-box {
  margin: 5px auto;
  width: 1200px;
  height: 280px;
  overflow: hidden;
  margin-bottom: 18px;
}
@mixin i-box {
  box-shadow: 0 0 1px #bebebe;
  height: 100%;
  overflow: hidden;
}
/*右边小框重复样式*/
@mixin litter_one_tab_box {
  @include i-box;
  width: 390px;
  height: 350px;
  float: right;
  margin: 10px 0;
}
.hot_search {
  @include adv;
  height:100%;
  padding:0 15px;
  //border: 1px solid rgba(204, 204, 204, 0.377);
  margin-left:15px;
}
.box1-left {
  width: 795px;
  float: left;
  //@include i-box;
  //border: 1px solid rgba(204, 204, 204, 0.377);
  margin-left:15px;
}
.box2-left {
  //@include i-box;

  margin: 10px 0;
  padding:0 15px;
}
.out_line {
  @include adv;
  height: 100%;
}
/*左边文章样式*/
.box3-left {
  //@include i-box;
  margin: 10px 0;
}
.box4-right {
  width: 390px;
  //@include i-box;
  float: right;
  margin-top:40px;
}
.box4-right>div:hover{
    box-shadow: -1px 1px 1px #bebebe;
  
}
.box5-right,
.box6-right,
.box7-right ,
.box8-right{
  @include litter_one_tab_box;
  box-shadow: 0 0 1px #bebebe;
  margin-right: 0.5px;
}
.box-paging {
  //@include i-box;
  height: 50px;
  margin: 10px 0;
}
</style>


