<template>
    <div id="enterpriseHome">
        <div id="location">
            当前位置：<router-link to="/home" class="selectable">服务平台</router-link> > <span class=red>企业</span>
        </div>
        <div id="enterpriseNews">
            <i class="fa fa-align-justify" aria-hidden="true"></i>
            <div class="more">更多</div>
            <div id="enterpriseNewsPics">
            <div v-for="i in newsPics" v-bind:src="i" class="newsPicFrame">
                <img class="newsPic" v-bind:src="i">
                <br>
                <div class="newsText">这是一段新闻介绍，北京申奥成功啦！这是一段新闻介绍，北京申奥成功啦!这是一段新闻介绍，北京申奥成功啦!这是一段新闻介绍，北京申奥成功啦!</div>
            </div>
            </div>
        </div>
        <div id="enterpriseDisplay">
            <div @click="routerLink('/enterprise/enterpriseHall')" class="more">更多</div>
            <div class="smallTitle">
                品牌展厅
            </div>
            <br>
            <div class="flexbox">
                <img v-for="i in enterpriseDisplay" v-bind:src="i" class="displayPic" @click="routerLink('/enterprise/enterpriseDisplay')"> 
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'enterpriseHome',
    data(){
        return {
            newsPics:["./static/PICS/timg5.jpg","./static/PICS/timg5.jpg"],
            enterpriseHot:["新松机器人科技天团亮相世界机器人大会",
                "2018年上边年广东工业机器人产量占全国1/5",
                "机器视觉公司扩博智能完成1100万美元融资",
                "2018世界机器人大会在北京举办"
                ],
            enterpriseDisplay:[
                "./static/PICS/company1.png",
                "./static/PICS/company2.png",
                "./static/PICS/company3.png",
                "./static/PICS/company1.png",
                "./static/PICS/company2.png",
                "./static/PICS/company3.png"
            ],
        }
    },
    methods:{
        routerLink:function(path){
            this.$router.push(path)
        }
    }
}
</script>
<style scoped>
    *{
        margin:0;
        padding:0;
    }
    .red{
        color:red;
    }
    .gray{
        color:gray
    }
    .gray:hover{
        color:red
    }
    .selectable:hover{
        color:red;
    }
    .smallTitle{
        float:left;
        display:inline-block;
    }
    div#location,div#enterpriseDisplay{
        padding:5px;
        width:800px;
        border:2px solid rgba(222,222,222,1);
        margin-bottom:10px;
    }
    div#enterpriseNews{
        display:inline-block;
        padding:5px;
        width:800px;
        border:2px solid rgba(222,222,222,1);
        position:relative;
        overflow:hidden;
    }
    div#enterpriseNews:after{
        content:"企业新闻";
        position:absolute;
        left:1.8rem;
        top:5px;
    }
    div#enterpriseNewsPics{
        width:800px;
        height:450px;
        margin-top:5px;
        overflow:hidden;
        white-space:nowrap;
    }
    div.newsPicFrame{
        margin:0;
        width:800px;
        height:450px;
        display:inline-block;
        position:absolute;
    }
    img.newsPic{
        vertical-align:bottom;
        width:800px;
        height:450px;
    }
    .newsText{
        color:white;
        position:absolute;
        font-size:1rem;
        height:1.8rem;
        line-height:1.8rem;
        bottom:0px;
        left:0px;
        width:790px;
        padding-left:5px;
        background:rgba(0,0,0,0.3);
        overflow:hidden;
        text-overflow:ellipsis;
        white-space:nowrap;
    }
    div#enterpriseDisplay{
    }
    div.flexbox{
        width:100%;
        display:flex;
        display: -webkit-flex; /* Safari */
        justify-content:center;
        flex-wrap:wrap;
    }
    img.displayPic{
        width:100px;
        height:100px;
        margin:15px;
    }
    .more{
        color:red;
        float:right;
        display:inline-block;
    }
    .more:hover{
        cursor:pointer;
    }
</style>



