<template>
    <div id="enterpriseDisplay">
        <div id="location">
            当前位置：<router-link to="/home" class="selectable">服务平台</router-link> > 
            <router-link to="/enterprise" class="selectable">企业</span></router-link> >
            <span class=red>xxx有限公司</span>
        </div>
    </div>
</template>

<script>
export default {
    name: 'enterpriseDisplay',
    data(){
        return {
            enterpriseDisplay:[
                "./static/PICS/company1.png",
                "./static/PICS/company2.png",
                "./static/PICS/company3.png",
                "./static/PICS/company1.png",
                "./static/PICS/company2.png",
                "./static/PICS/company3.png"
            ],
        }
    },
    methods:{
        routerLink:function(path){
            this.$router.push(path)
        }
    }
}
</script>
<style scoped>
    *{
        margin:0;
        padding:0;
    }
    .red{
        color:red;
    }
    .gray{
        color:gray
    }
    .gray:hover{
        color:red
    }
    .selectable:hover{
        color:red;
    }
    .smallTitle{
        float:left;
        display:inline-block;
    }
    div.flexbox{
        width:100%;
        display:flex;
        display: -webkit-flex; /* Safari */
        justify-content:center;
        flex-wrap:wrap;
    }
    img.displayPic{
        width:100px;
        height:100px;
        margin:15px;
    }
    .more{
        color:red;
        float:right;
        display:inline-block;
    }
    .more:hover{
        cursor:pointer;
    }
    div#location{
        padding:5px;
        width:800px;
        border:2px solid rgba(222,222,222,1);
        margin-bottom:10px;
    }
</style>



