<template>
    <div id="enterpriseDisplay">
        <div id="location">
            当前位置：<router-link to="/home" class="selectable">服务平台</router-link> > 
            <router-link to="/enterprise" class="selectable">企业</span></router-link> >
            <span class=red>品牌展厅</span>
        </div>
        <div class="bigTitle">知名品牌</div>
        <div id="famous" class="flexbox">
            <div v-for="i in famous" class="famousFrame">
                <img v-bind:src="i" class="famousPic">
                <br>
                品牌名称
            </div>
        </div>
        <div class="bigTitle">会员品牌</div>
        <div id="member" class="flexbox">
        </div>
        <div class="bigTitle">其他入驻品牌</div>
        <div id="others" class="flexbox">
        </div>
    </div>
</template>

<script>
export default {
    name: 'enterpriseDisplay',
    data(){
        return {
            count:1,
            enterpriseDisplay:[ 
                "../static/PICS/company1.png",
                "../static/PICS/company2.png",
                "../static/PICS/company3.png",
                "../static/PICS/company1.png",
                "../static/PICS/company2.png",
                "../static/PICS/company3.png"
            ],
            famous:[ 
                "../static/PICS/company1.png",
                "../static/PICS/company2.png",
                "../static/PICS/company3.png",
            ],
        }
    },
    methods:{
        routerLink:function(path){
            this.$router.push(path)
        }
    }
}
</script>
<style scoped>
    *{
        margin:0;
        padding:0;
    }
    .red{
        color:red;
    }
    .gray{
        color:gray
    }
    .gray:hover{
        color:red
    }
    .selectable:hover{
        color:red;
    }
    .smallTitle{
        float:left;
        display:inline-block;
    }
    div.flexbox{
        width:100%;
        display:flex;
        display: -webkit-flex; /* Safari */
        justify-content:center;
        flex-wrap:wrap;
        border:2px solid rgba(222,222,222,1);
        border-top:0;
        min-height:100px;
        margin-bottom:10px;
    }
    .more{
        color:red;
        float:right;
        display:inline-block;
    }
    .more:hover{
        cursor:pointer;
    }
    div#location,div.bigTitle{
        padding:5px;
        width:800px;
        border:2px solid rgba(222,222,222,1);
        margin-bottom:10px;
    }
    div.bigTitle{
        padding:15px 5px;
        text-align:center;
        font-size:1.8rem;
        font-weight:bold;
        letter-spacing:6px;
        margin:0;
    }
    div.famousFrame{
        text-align:center;
    }
    img.famousPic{
        height:150px;
        width:150px;
        margin:20px;
    }
</style>



