<template>
  <div id="enterprise">
    <!-- <m-header></m-header> -->
    <div id="left">
      <router-view></router-view>
    </div>
    <div id="right">
      <div id="enterpriseHot">
        <div class="more">更多</div>
        <div id="hotTitle">
          <router-link to="/enterprise" v-for="(i,key) in enterpriseHot" :key="key" class="gray"><br>{{i}}<br></router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import MHeader from '@/components/common/m-header'
import enterpriseHome from '@/components/enterprise/enterpriseHome'

export default {
  name: "enterprise",
  data(){
    return {
      newsPics:["./static/PICS/timg5.jpg","./static/PICS/timg5.jpg"],
      enterpriseHot:["新松机器人科技天团亮相世界机器人大会",
        "2018年上边年广东工业机器人产量占全国1/5",
        "机器视觉公司扩博智能完成1100万美元融资",
        "2018世界机器人大会在北京举办"
        ]
    }
  },
  components: {
    MHeader,
    enterpriseHome,
  },
}
</script>

<style lang="scss" scoped>

  div#enterprise{
    width:1200px;
    margin:0 auto;
  }
  div#left{
    float:left;
    position:relative;
    top:10px;
    width:810px;
  }
  div#right{
    float:right;
    position:relative;
    top:10px;
    width:380px;
  }
  .red{
    color:red;
  }
  .gray{
    color:gray
  }
  .gray:hover{
    color:red
  }
  .selectable:hover{
    color:red;
  }
  .more{
    color:red;
    float:right;
    display:inline-block;
  }
  .more:hover{
    cursor:pointer;
  }
  //以下是右边的

  div#enterpriseHot{
    width:auto;
    padding:5px;
    border:2px solid rgb(222,222,222);
  }
  div#enterpriseHot:after{
    content:"企业热点";
    position:absolute;
    top:5px;
    left:1.5rem;
  }
  div#enterpriseHot:before{
    content:"";
    width:100%;
    position:absolute;
    top:2rem;
    left:0;
    border-top:2px solid rgba(222,222,222,1);
  }
  div#enterpriseHot>div.more{
    margin-bottom:10px;
  }
  div#hotTitle{
    position:relative;
    top:1.9rem;
    margin-bottom:2.9rem;
  }
</style>




